:root {
  --background-color: #fefefe;
  --color: #343434;
}

body {
  margin: 0;
  background-color: var(--background-color);
  color: var(--color);
  font-family: sans-serif;
  transition: all 200ms linear;
}
@media screen and (max-width: 700px) {
  .App {
    flex-direction: column;
  }
  #Editor {
    width: 90vw !important;
  }
  .editor-holder {
    width: 90vw !important;
  }
}
.App {
  margin: 0;
  display: flex;
  /* text-align: center; */
}

.title {
  text-align: center;
  font-size: 30px;
}
.pane {
  flex-direction: row;
  width: 50vw !important;
  margin: 14px;
}
.Output {
  background: white;
  color: white;
  width: 90vw !important;
}
#myFrame {
  box-shadow: red -1px -2px 7px;
}
.ace_editor {
  height: 90vh !important;
  width: 48vw !important;
  /* margin:10px; */
}
.editor-container {
  flex-grow: 1;
  flex-basis: 0;
  display: flex;
  flex-direction: column;
  padding: 0.5rem;
  /* color: white; */

  /* background-color: hsl(225, 6%, 25%); */
}

/* Dark mode toggle button */
.editor-holder {
  box-shadow: #7fff00 0px 1px 4px;
}
.editorController {
  display: flex;
  justify-content: space-around;
}
.editorController button {
  background-color: DodgerBlue;
  border: none;
  color: white;
  padding: 5px;
  cursor: pointer;
  font-size: 15px;
}
#run {
  background-color: #5cdb5c;
}
.switch {
  display: flex;
  height: 31px;
  position: relative;
  width: 64px;
}

.switch input {
  display: none;
}

.slider {
  background-color: #fff;
  border: 1px solid #000;
  bottom: 0;
  cursor: pointer;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  transition: 0.4s;
}

.slider:before {
  background-color: #333;
  border: 1px solid #000;
  bottom: 0.5px;
  content: "";
  height: 26px;
  left: 1px;
  position: absolute;
  transition: 0.4s;
  width: 26px;
  z-index: 421;
}

input:checked + .slider {
  background-color: #5cdb5c;
}

input:checked + .slider:before {
  transform: translateX(34px);
  border-color: #cfcfcf;
  background-color: #efefef;
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

.toggle-moon,
.toggle-sun {
  width: 50%;
  text-align: center;
  padding: 0.25em;
  position: relative;
  z-index: 420;
}
